html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-family: sans-serif;
}

* {
  box-sizing: border-box;
  word-wrap: break-word;
  outline: none;
  font-family: "Raleway", sans-serif;
  font-weight: 600;
}

img {
  object-fit: cover;
}

button {
  cursor: pointer;
  border: none;
}

i {
  cursor: pointer;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}
